import * as React from 'react'
import Button from 'antd/lib/button';import 'antd/lib/button/style/css.js';import Input from 'antd/lib/input';import 'antd/lib/input/style/css.js';import message from 'antd/lib/message';import 'antd/lib/message/style/css.js';

interface IProps {
  addIdentity?: any
}

class AddUser extends React.Component<IProps>{
  constructor(props: IProps) {
    super(props)
  }
  state = {
    identity_text: '',
  }
  handAddUserValue = (e: any) => {
    let { value } = e.target;
    this.setState({
      identity_text: value
    })
  }
  render() {
    let { identity_text } = this.state;
    return (
      <div className="adduser-wrapper">
        <div className="adduser-input">
          <Button>添加身份</Button>
        </div>
        <div className="adduser-input">
          <Input placeholder="请输入身份名称" name="identity_text" value={identity_text} onChange={this.handAddUserValue} />
        </div>
        <div className="adduser-input">
          <Button type="primary" className="btn-active" onClick={this.handClickSf}>确定</Button>
          <Button onClick={this.handClear}>重置</Button>
        </div>
      </div>
    )
  }
  handClear=()=>{
    this.setState({
      identity_text:''
    })
  }
  //添加身份
  handClickSf = async () => {
    let { identity_text } = this.state;
    let { addStatus } = this.props.addIdentity;
    if(identity_text===''){
      message.error('请输入身份名称')
    }else{
      let result = await addStatus({
        "identity_text": identity_text
      })
      if (result.code === 1) {
        message.success(result.msg)
      } else {
        message.error(result.msg)
      }
    }
    
  }
}

export default AddUser


